<template>
  <div
    class="sticky top-spacing-0 z-[1] flex justify-between bg-bg-neutral-default py-spacing-200 ipad:items-center"
  >
    <div
      class="flex flex-col gap-spacing-100 ipad:flex-row ipad:items-center ipad:gap-spacing-200"
      v-if="company?.companyName"
    >
      <h1
        class="text-headline/s/relaxed/500 text-constant-brand-primary ipad:text-headline/m/relaxed/500"
      >
        {{ company?.companyName }}
      </h1>
      <div @click="handleEdit()">
        <GeneralButton
          :button-type="'button'"
          :type="'brand-secondary'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="false"
          :is-loading="false"
          :label="'Edit Details'"
        />
      </div>
      <div @click="handleDelete()">
        <GeneralButton
          :button-type="'button'"
          :type="'destructive'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="false"
          :is-loading="false"
          :label="'Delete'"
        />
      </div>
    </div>
    <span class="text-headline/s/relaxed/500 text-constant-status-warning"
      ><span class="text-headline/s/relaxed/500 text-constant-brand-primary"
        >GS </span
      >{{ company?.GS }}</span
    >
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  props: {
    company: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  components: {
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },

  mounted() {
    this.$nextTick(() => {
      requestAnimationFrame(() => {
        const edit = this.$route?.query?.edit;
        if (edit) {
          this.handleEdit(true);
        }
      });
    });
  },
  methods: {
    ...mapActions("company", ["deleteCompany"]),
    handleEdit(goBack) {
      this.$bus.$emit("add-new-company-modal", {
        showModal: true,
        companyId: this.company?._id,
        formData: {
          companyName: this.company?.companyName,
          genNumber: this.company?.genNumber,
          commNumber: this.company?.commNumber,
          email: this.company?.email,
          annualRevenue: this.company?.annualRevenue,

          // company origin details
          origin: this.company?.origin,
          originList: this.company?.originList,

          // location
          location: {
            display_name: this.company?.location,
            lat: this.company?.lat,
            lon: this.company?.long,
          },

          GS: this.company?.GS,
          RS: this.company?.RS,

          agents: this.company?.agents?.map((item) => ({
            genNumber: item?.genNumber,
            commNumber: item?.commNumber,
            email: item?.email,
          })),

          products: this.company?.products?.map((item) => ({
            originalTitle: item?.productId?.title,
            _id: item?.productId?._id,
          })),
        },
        goBack,
        update: true,
      });
    },
    async handleDelete() {
      let payload = {
        id: this.company?._id,
      };
      const response = await this.deleteCompany(payload);
      if (response?.success) {
        this.$toast.success(response?.message);
        this.$router.push("/dashboard/companies");
      } else {
        this.$toast.error(response?.message);
      }
    },
  },
};
</script>

<style scoped></style>
